import React from 'react';

import Arrow from '../arrow/arrow.component';
import LongStep from '../long-step/long-step.component.jsx';
import ShortStep from '../short-step/short-step.component';

import './graph-structure.styles.scss';

function GraphStructure({stepsData1, stepsData2, stepsData3, longSteps}) {

    const [currentStep, setCurrentStep] = React.useState({});
    const [stepWindowVisible, setStepWindowwVisible] = React.useState(false);
    const windowSwitch = () => {
        setStepWindowwVisible((prevState) => !prevState)
    }

    const windowSwitchTrue = () => {
        setStepWindowwVisible(true);
    }



    let title,description,purpose, actions, success, nextSteps, notes, meetings=[], research,image;
    if(currentStep){
        ({title,description,purpose, actions, success, nextSteps, notes, meetings = [], research,image} = currentStep);
        console.log("m", meetings)
    }
    return (
        <div className="graph-structure">
            <div className="short-step-row">
                {stepsData1.map((step, i) => <React.Fragment key={i}><Arrow text={step.type !== 'arrow' ? '->' : ''}/><ShortStep step={step} setCurrentStep={setCurrentStep} windowSwitch={windowSwitch} windowSwitchTrue={windowSwitchTrue} /></React.Fragment> )}
            </div>
            <div className="short-step-row">
                {stepsData2.map((step, i) => <React.Fragment key={i}><Arrow text={step.type !== 'arrow' ? '->' : ''}/><ShortStep step={step} setCurrentStep={setCurrentStep} windowSwitch={windowSwitch} windowSwitchTrue={windowSwitchTrue} /></React.Fragment> )}
            </div>
            <div className="short-step-row">
                {stepsData3.map((step, i) => <React.Fragment key={i}><Arrow text={step.type !== 'arrow' ? '->' : ''}/><ShortStep step={step} setCurrentStep={setCurrentStep} windowSwitch={windowSwitch} windowSwitchTrue={windowSwitchTrue} /></React.Fragment> )}
            </div>
            {longSteps.map((longStep, i) => <div key={i} className="long-step-row">
                <LongStep step={longStep} setCurrentStep={setCurrentStep} windowSwitch={windowSwitch} windowSwitchTrue={windowSwitchTrue} />
            </div>)}
            {stepWindowVisible && currentStep && <div className="short-step-big-window">
                <div className="title-line">
                    <div className="title"  onClick={windowSwitch}><p>{title}</p></div>
                    <div className="close-window" onClick={windowSwitch}><span className="bold">X</span></div>
                </div>
                <div className="description">
                    <div className="small-title bold">Description</div>
                    {description}
                </div>
                <div className="description">
                    <div className="small-title bold">Purpose</div>
                    {purpose}
                </div>
                <div className="description">
                    <div className="small-title bold">Actions</div>
                    {actions}
                </div>
                <div className="description">
                    <div className="small-title bold">Success</div>
                    {success}
                </div>
                {nextSteps && nextSteps.length >0 && <div className="description">
                    <div className="small-title bold">Next steps</div>
                    {nextSteps.map((s) => <p>{s} </p>)}
                </div>}
                {notes && <div className="notes">
                    <div className="bold">Notes:</div>
                    {notes}
                </div>}
                <div className="actions">
                    {meetings && meetings.length !== 0 && <React.Fragment>
                        <div className="small-title bold">Topics for meetings:</div>
                        {meetings.map((meeting, i) => {
                            return <React.Fragment key={i}>
                                    <div className="meeting little-bold"><span className="bold">{i+1}:</span> {meeting.title}</div>
                                    <div className="description">{meeting.description}</div>
                                    {/*<div className="add-to"> + Add to meeting </div>*/}
                                </React.Fragment>
                        })}  
                    </React.Fragment>}
                    {research && research.length !== 0 && <React.Fragment>
                        <div className="bold">Topics for research:</div>
                        {research.map((researchS, i) => {
                            return <React.Fragment key={i}>
                                    <div className="meeting little-bold"><span className="bold">{i+1}:</span> {researchS.title}</div>
                                    <div className="description">{researchS.description}</div>
                                </React.Fragment>
                        })}  
                    </React.Fragment>}
                </div>
                <div className='image'>
                    <img src={image} alt="something fitting to the topic, usually with trees"/>
                </div>
            </div>}
        </div>
    )
}

export default GraphStructure;