import React, {useEffect} from 'react';

import GraphStructure from '../graph-structure/graph-structure.component';
import MeetingsStructure from '../meetings-structure/meetings-structure.component';

import './graph-view.styles.scss';

import {steps1, steps2, steps3, longSteps} from '../../data/steps';
import {meetings} from '../../data/meetings';

function GraphView() {
    const [stepsData1, setStepsData1] = React.useState([]);
    const [stepsData2, setStepsData2] = React.useState([]);
    const [stepsData3, setStepsData3] = React.useState([]);
    const [meetingsData, setMeetingsData] = React.useState([]);

    useEffect(()=> {
        setStepsData1(steps1)
        setStepsData2(steps2)
        setStepsData3(steps3)
        setMeetingsData(meetings)
    }, [])
    return (
        <div className="container">
            <div className="top-navigation">
            </div>
            <div className="main-box">
                <div className="left-panel">
                    <GraphStructure stepsData1={stepsData1} stepsData2={stepsData2} stepsData3={stepsData3} longSteps={longSteps}></GraphStructure>
                </div>
                <div className="right-panel">
                    <MeetingsStructure meetings={meetingsData}></MeetingsStructure>
                </div>
            </div>
            <div className="footer">
            </div>
        </div>
    )
}

export default GraphView;