import React from 'react';

import Meeting from '../meeting/meeting.component';

import './meetings.structure.styles.scss';

function MeetingsStructure({meetings}) {
    return (
        <div className="meetings-structure">
            <div className="meetings-title">Meetings</div>
            {meetings && meetings.map((meeting, i)=> <Meeting key={i} meeting={meeting} />)}
        </div>
    )
}

export default MeetingsStructure;