import React from 'react';

import './meeting.styles.scss';


function Meeting({meeting}) {
    const {title, description,notes,pad, location} = meeting;
    return (
        <div className="meeting-event">
            <div className="title"><p>{title}</p></div>
            <div className="location"><p><span className="bold">Location: </span>{location}</p></div>
            <div className="description">{description}</div>
            <div className="agenda">
                <div className="agenda-element"> {}</div>
            </div>
            <div className="notes">{notes}</div>
            <div className="pad"><span className="bold">Pad: </span><a href={pad}>{pad}</a></div>
        </div>
    )
}

export default Meeting;