import React from 'react';

import './short-step.styles.scss';


function ShortStep({step, windowSwitch, windowSwitchTrue, setCurrentStep}) {

    const {type, title, shortDescription} = step;

    if(type === "arrow"){
        return (
            <div className="short-step arrow-step">
        </div>
        )
    }

    const switchWindow = async () => {
        await windowSwitchTrue();
        await setCurrentStep(step);
    }

    return (
        <React.Fragment>
            <div className="short-step" onClick={switchWindow}>
                <div className="title"><p>{title}</p></div>
                <div className="description">{shortDescription}</div>
                <div className="actions">
                </div>
            </div>
        </React.Fragment>
    )
}

export default ShortStep;