import React from 'react';

import './long-step.styles.scss';


function LongStep({step, windowSwitch, setCurrentStep}) {
    const {title, description} = step;
    const switchWindow = () => {
        windowSwitch(true);
        setCurrentStep(step);
    }
    return (
        <div className="long-step" onClick={switchWindow}>
            <div className="title">{title}</div>
            <div className="description">{description}</div>
        </div>
    )
}

export default LongStep;